import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/financeiro',
    name: 'FinancialControl',
    component: () => import('../views/FinancialControl.vue')
  },
  {
    path: '/about',
    name: 'about',
    component: () => import('../views/AboutView.vue')
  },
  {
    path: '/transportadoras',
    name: 'CompanyList',
    component: () => import('../views/CompanyList.vue')
  },
  {
    path: '/usuarios',
    name: 'UserList',
    component: () => import('../views/UserList.vue')
  },
  {
    path: '/login',
    name: 'LoginScreen',
    component: () => import('../views/LoginScreen.vue'),
    meta: 'login'
  },
  {
    path: '/veiculos',
    name: 'VehicleList',
    component: () => import('../views/VehicleList.vue')
  },
  {
    path: '/pedidos',
    name: 'OrderList',
    component: () => import('../views/OrderList.vue')
  },
  {
    path: '/novo-pedido-1',
    name: 'NewOrderCustomer',
    component: () => import('../views/NewOrderCustomer.vue')
  },
  {
    path: '/novo-pedido-2',
    name: 'NewOrderStaff',
    component: () => import('../views/FinancialControl.vue')
  },
  {
    path: '/testes',
    name: 'ViaCep',
    component: () => import('../components/ViaCep.vue')
  },
]

const router = new VueRouter({
  routes
})

export default router